<template>
    <el-container style="width:100vw; height: 100vh;">
<!--        <el-header style="background:#ffffff;display: flex; height: 40px; align-items: center;">-->
<!--            <div class="upload-icon" @click="chooseFile">-->
<!--                <div class="upload-icon">打开OFD</div>-->
<!--                <font-awesome-icon icon="cloud-upload-alt"/>-->
<!--                <input type="file" ref="file" class="hidden" accept=".ofd" @change="chooseFileChanged">-->
<!--            </div>-->
<!--        </el-header>-->
        <el-main style="height: auto;background: #ffffff;padding: 0" v-loading="loading"  id="main">
            <div class="main-section"
                 id="content" ref="contentDiv" @mousewheel="scrool">
            </div>
        </el-main>

    </el-container>
</template>

<script>
    import {parseOfdDocument, renderOfd, getPageScale} from "@/utils/ofd/ofd";
    import * as JSZipUtils from "jszip-utils";

    export default {
        name: "HomePage",
        data() {
            return {
                file:null,
                loading: false,
                pageIndex: 1,
                pageCount: 0,
                scale: 0,
                ofdObj: null,
                screenWidth: document.body.clientWidth,
            }
        },

        created() {
            this.file = null;
        },

        mounted() {
            this.screenWidth = document.body.clientWidth;
            let that = this;
            this.$refs.contentDiv.addEventListener('scroll', this.scrool);
            window.onresize = () => {
                return (() => {
                   that.screenWidth = document.body.clientWidth;
                   const divs = renderOfd(that.screenWidth, that.ofdObj);
                   that.displayOfdDiv(divs);
                })()
            };
            window.callJsUpdateOfdFunction = this.callJsUpdateOfdFunction

        },

        methods:{

            callJsUpdateOfdFunction(path){
                this.updateOfd(path);
            },

            updateOfd(ofdFile) {
                let that = this;
                JSZipUtils.getBinaryContent(ofdFile, function (err, data) {
                    if (err) {
                        console.log(err)
                    } else {
                        let base64String = btoa(String.fromCharCode.apply(null, new Uint8Array(data)));
                        that.ofdBase64 = base64String;
                    }
                });
                this.getOfdDocumentObj(ofdFile, this.screenWidth);
            },

            chooseFile() {
                this.file = null;
                this.$refs.file.click();
            },

            chooseFileChanged() {
                this.file = this.$refs.file.files[0];
                this.processChooseFile();
            },

            processChooseFile() {
                let ext = this.file.name.replace(/.+\./, "");
                if (["ofd"].indexOf(ext) === -1) {
                    this.$alert('error', '仅支持ofd类型', {
                        confirmButtonText: '确定',
                        callback: action => {
                            this.$message({
                                type: 'info',
                                message: `action: ${ action }`
                            });
                        }
                    });
                    return;
                }
                if (this.file.size > 100 * 1024 * 1024) {
                    this.$alert('error', '文件大小需 < 100M', {
                        confirmButtonText: '确定',
                        callback: action => {
                            this.$message({
                                type: 'info',
                                message: `action: ${ action }`
                            });
                        }
                    });
                    return;
                }
                let that = this;
                let reader = new FileReader();
                reader.readAsDataURL(this.file);
                reader.onload = function (e) {
                    that.ofdBase64 = e.target.result.split(',')[1];
                };
                this.getOfdDocumentObj(this.file, this.screenWidth);
                this.$refs.file.value = null;
            },


            getOfdDocumentObj(file, screenWidth) {
                let that = this;
                this.loading = true;
                parseOfdDocument({
                    ofd: file,
                    success(res) {
                        that.ofdObj = res[0];
                        that.pageCount = res[0].pages.length;
                        const divs = renderOfd(screenWidth, res[0]);
                        that.displayOfdDiv(divs);
                        that.loading = false;
                    },
                    fail(error) {
                        that.loading = false;
                        that.$alert('OFD打开失败', error, {
                            confirmButtonText: '确定',
                            callback: action => {
                                this.$message({
                                    type: 'info',
                                    message: `action: ${ action }`
                                });
                            }
                        });
                    }
                });
            },

            displayOfdDiv(divs) {
                this.scale = getPageScale();
                let contentDiv = document.getElementById('content');
                contentDiv.innerHTML = '';
                for (const div of divs) {
                    contentDiv.appendChild(div)
                }
            },

            addEventOnSealDiv(div, SES_Signature, signedInfo) {
                try {
                    global.HashRet=null;
                    global.VerifyRet=signedInfo.VerifyRet;
                } catch (e) {
                    console.log(e);
                }
                if (!global.VerifyRet) {
                    div.setAttribute('class', 'gray');
                }
            },

            scrool() {
                let scrolled = this.$refs.contentDiv.firstElementChild?.getBoundingClientRect()?.top - 60;
                let top = 0;
                let index = 0;
                for (let i=0;i<this.$refs.contentDiv.childElementCount; i ++) {
                    top += (Math.abs(this.$refs.contentDiv.children.item(i)?.style.height.replace('px','')) + Math.abs(this.$refs.contentDiv.children.item(i)?.style.marginBottom.replace('px','')));
                    if (Math.abs(scrolled) < top) {
                        index = i;
                        break;
                    }
                }
                this.pageIndex = index+1;
            },
        },
    }
</script>

<style scoped>

    .upload-icon {
        display: flex;
        cursor: pointer;
        justify-content: center;
        align-items: center;
        height: 28px;
        padding-left: 10px;
        padding-right: 10px;
        background-color: rgb(59, 95, 232);
        border-radius: 1px;
        border-color: #5867dd;
        font-weight: 500;
        font-size: 12px;
        color: white;
        margin: 1px;
    }
    .hidden {
        display: none !important;
    }

    .main-section {
        padding-top: 0px;
        margin-left:0px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: #ffffff;
        overflow: hidden
    }

</style>
